.navigation {
  width: 50%;
  display: flex;
  flex-direction: column;

  &__heading {
    margin: 0 0 25px 0;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-size: 15px;
    line-height: 16px;
    text-transform: uppercase;
    color: $color-tertiary;
  }

  &__list {
    margin: 0;
    padding: 0;
    font-size: 15px;
    line-height: 25px;
    color: $color-tertiary-light;
    list-style: none;
    columns: 2;
  }

  & li {
    display: inline-block;
    vertical-align: middle;
    width: 100%;
  }

  @media (max-width: $tablet-max-width) {
    padding: 35px 0 35px 25px;

    &__heading {
      margin: 0;
    }

    &__list {
      padding-top: 30px;
      columns: 1;
    }

    & li {
      display: block;
      width: auto;
    }
  }

  @media (max-width: $mobile-max-width) {
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    padding: 25px 15px 25px 15px;
    border-bottom: 1px solid $color-border;

    &__button {
      margin-left: auto;

      &--no-js {
        display: none;
      }
    }

    &__list {
      display: none;
      width: 100%;

      &--no-js {
        display: block;
      }
    }
  }
}
