body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  height: 100%; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip: rect(0 0 0 0);
  overflow: hidden; }

.header {
  display: flex;
  justify-content: space-between;
  padding: 33px 10px;
  background-color: #1B2D37; }
  .header__container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 1411px;
    margin: 0 auto; }
  .header__container-inner {
    display: flex;
    justify-content: space-between; }
    .header__container-inner--left {
      width: 35%; }
    .header__container-inner--right {
      width: 65%; }
  @media (max-width: 1023px) {
    .header {
      padding: 22px 25px; }
      .header__button {
        display: none; } }
  @media (max-width: 1023px) {
    .header {
      padding: 18px; } }

.button {
  padding: 13px 26px 14px;
  border: 1px solid #fff;
  border-radius: 5px;
  font-family: "Roboto Condensed Bold", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.7px;
  text-align: center;
  text-transform: uppercase;
  color: #fff;
  background-color: transparent;
  white-space: nowrap;
  cursor: pointer; }

.logo {
  display: flex;
  align-items: center; }
  .logo__text-wrap {
    display: flex;
    flex-direction: column;
    margin-left: 16px; }
  .logo__company-name {
    font-family: "Roboto Condensed Bold", "Arial", sans-serif;
    font-size: 25px;
    line-height: 29px;
    color: #fff;
    white-space: nowrap; }
    .logo__company-name--footer {
      color: #1B2D37; }
  .logo__slogan {
    font-family: "Roboto", "Arial", sans-serif;
    font-size: 12px;
    line-height: 14px;
    color: #788891; }
  @media (max-width: 767px) {
    .logo--footer {
      align-items: flex-start; }
    .logo__text-wrap {
      display: none; }
      .logo__text-wrap--footer {
        display: flex;
        flex-direction: column;
        margin-bottom: 80px; }
    .logo__icon {
      width: 38px;
      height: 38px; }
      .logo__icon--footer {
        width: 52px;
        height: 52px; } }

.contacts {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0 38px 0 auto; }
  @media (max-width: 1023px) {
    .contacts {
      margin: 0 0 0 auto; } }
  .contacts__item {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: #fff; }
    .contacts__item:not(:last-child) {
      margin-right: 88px; }
    @media (max-width: 1023px) {
      .contacts__item--email svg {
        width: 25px;
        height: 18px; }
      .contacts__item--phone svg {
        width: 24px;
        height: 24px; }
      .contacts__item:not(:last-child) {
        margin-right: 33px; } }
    @media (max-width: 767px) {
      .contacts__item:not(:last-child) {
        margin-right: 23px; } }

.contacts span {
  margin-left: 10px;
  line-height: 18px;
  white-space: nowrap; }
  @media (max-width: 1023px) {
    .contacts span {
      display: none; } }

.footer {
  flex: 0 0 auto;
  background-color: #FAFAFA; }
  .footer__wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 1411px;
    margin: 0 auto;
    padding: 60px 20px 50px 20px; }
  .footer__inner {
    width: 25%;
    display: flex;
    flex-direction: column;
    justify-content: space-between; }
  @media (max-width: 1023px) {
    .footer {
      position: relative; }
      .footer__wrapper {
        flex-wrap: wrap;
        padding: 0; }
      .footer__inner {
        flex-direction: row;
        align-items: center;
        width: 100%;
        padding: 19px 25px;
        border-bottom: 1px solid rgba(98, 105, 98, 0.2); } }
  @media (max-width: 767px) {
    .footer__inner {
      flex-direction: column;
      padding: 46px 43px 54px 43px; } }

.social {
  display: flex;
  margin: 0;
  padding: 0;
  list-style: none; }
  .social__item:not(:last-child) {
    margin-right: 15px; }

.navigation {
  width: 50%;
  display: flex;
  flex-direction: column; }
  .navigation__heading {
    margin: 0 0 25px 0;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-size: 15px;
    line-height: 16px;
    text-transform: uppercase;
    color: #1B2D37; }
  .navigation__list {
    margin: 0;
    padding: 0;
    font-size: 15px;
    line-height: 25px;
    color: rgba(27, 45, 55, 0.5);
    list-style: none;
    columns: 2; }
  .navigation li {
    display: inline-block;
    vertical-align: middle;
    width: 100%; }
  @media (max-width: 1023px) {
    .navigation {
      padding: 35px 0 35px 25px; }
      .navigation__heading {
        margin: 0; }
      .navigation__list {
        padding-top: 30px;
        columns: 1; }
      .navigation li {
        display: block;
        width: auto; } }
  @media (max-width: 767px) {
    .navigation {
      flex-direction: row;
      flex-wrap: wrap;
      width: 100%;
      padding: 25px 15px 25px 15px;
      border-bottom: 1px solid rgba(98, 105, 98, 0.2); }
      .navigation__button {
        margin-left: auto; }
        .navigation__button--no-js {
          display: none; }
      .navigation__list {
        display: none;
        width: 100%; }
        .navigation__list--no-js {
          display: block; } }

.contact-info {
  width: 25%;
  display: flex;
  flex-direction: column; }
  .contact-info__heading {
    margin: 0 0 25px 0;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-size: 15px;
    line-height: 16px;
    text-transform: uppercase;
    color: #1B2D37; }
  .contact-info__wrap {
    font-size: 15px;
    line-height: 25px;
    color: rgba(27, 45, 55, 0.5); }
  .contact-info p {
    margin: 0; }
  @media (max-width: 1023px) {
    .contact-info {
      width: 50%;
      padding: 35px 25px 35px 0; } }
  @media (max-width: 767px) {
    .contact-info {
      flex-direction: row;
      flex-wrap: wrap;
      width: 100%;
      padding: 25px 15px 25px 15px; }
      .contact-info__heading {
        margin: 0; }
      .contact-info__wrap {
        display: none;
        width: 100%;
        padding: 30px 40px 0 0; }
        .contact-info__wrap--no-js {
          display: block; }
      .contact-info__button {
        margin-left: auto; } }

.copyright {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1411px;
  margin: 0 auto;
  padding: 14px 20px;
  border-top: 1px solid rgba(98, 105, 98, 0.2);
  font-size: 13px;
  line-height: 24px;
  text-transform: uppercase;
  color: rgba(27, 45, 55, 0.5); }
  .copyright__rights {
    margin-right: 60px; }
  .copyright__year {
    white-space: nowrap; }
  .copyright__wrap-right {
    display: flex;
    justify-content: space-between;
    width: calc(50% + 108px); }
  @media (max-width: 1023px) {
    .copyright {
      padding: 21px 27px 24px 27px; }
      .copyright__rights {
        display: none; }
      .copyright__year {
        position: absolute;
        top: 35px;
        left: calc(50% + 50px);
        transform: translateX(-50%); }
      .copyright__wrap-right {
        width: 100%; } }
  @media (max-width: 767px) {
    .copyright__year {
      position: absolute;
      top: 124px;
      left: calc(50%);
      transform: translateX(-50%); }
    .copyright__wrap-right {
      flex-direction: column;
      justify-content: center;
      align-self: center;
      width: 100%; }
    .copyright__developer, .copyright__conditions {
      text-align: center; } }

.container {
  display: flex;
  flex-direction: column;
  min-height: 100%; }

.main {
  flex: 1 0 auto; }

.button-show {
  display: none;
  width: 16px;
  height: 16px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  outline: none; }
  @media (max-width: 767px) {
    .button-show {
      display: block;
      position: relative; }
      .button-show--no-js {
        display: none; }
      .button-show::before {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        content: "";
        width: 16px;
        height: 1px;
        background-color: #C4C4C4; }
      .button-show::after {
        position: absolute;
        top: 50%;
        right: 7px;
        transform: translateY(-50%);
        content: "";
        width: 1px;
        height: 16px;
        background-color: #C4C4C4; } }
  .button-show:hover::before, .button-show:hover::after, .button-show:focus::before, .button-show:focus::after {
    background-color: #666666; }
  .button-show--active::after {
    display: none; }
